.card-container {
    display: flex;
    justify-content: space-between; /* Allinea le card orizzontalmente */
    background-color: black; /* Imposta lo sfondo nero */
    overflow: hidden; /* Rimuove la barra di scorrimento orizzontale */
  }
  
  .card {
    width: 20%; /* Imposta una larghezza fissa in modo che ci siano sempre 5 card sulla stessa riga */
    margin-right: 20px; /* Aggiunge uno spazio tra le card */
    background-color: #fff; /* Imposta lo sfondo bianco per le card */
  }
  
  /* Stili delle card, puoi personalizzarli ulteriormente */
  .card {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 10px;
  }
  
  .card img {
    width: 100%;
    height: auto;
  }
  
  /* Stili responsivi per schermi pi첫 piccoli */
  @media (max-width: 768px) {
    .card-container {
      flex-wrap: wrap; /* Permette il wrapping delle card su schermi pi첫 piccoli */
      justify-content: center; /* Allinea le card centralmente orizzontalmente */
    }
  
    .card {
      width: calc(50% - 20px); /* Riduce la larghezza delle card su schermi pi첫 piccoli in modo che ce ne siano 2 per riga */
      margin-right: 20px;
      margin-bottom: 20px; /* Aggiunge spazio tra le card su schermi pi첫 piccoli */
    }
  }
  